/* ==========================================================================
 # Media-text block
========================================================================== */

/* Front-end and back-end
----------------------------------------------- */

.block-editor .wp-block[data-type="core/media-text"],
.wysiwyg .wp-block-media-text {
  background: $grey-light;
  h2, h3, h4 {
    @include heading(m);
    margin-bottom: $gap-medium;
  }
  &.alignfull,
  &[data-align="full"] {
    h2, h3, h4 {
      @include heading(l);
      @include spacing(margin-bottom);
    }
  }
  .wp-block-media-text__media {
    margin: 0;
    padding: 0;
    align-self: stretch;
    img {
      display: block;
      height: 100%;
      object-fit: cover;
      width: 100%;
    }
  }
  .wp-block-media-text__content {
    @include child-margin-reset;
    padding: $gap-big;
  }
}

/* Only front-end
----------------------------------------------- */

.wysiwyg .wp-block-media-text {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  &__content {
    width: 100%;
    @include breakpoint ($m) {
      width: 50%;
    }
    & > * {
      max-width: calc(#{$max-width-l} / 2);
    }
  }
  &__media {
    @include breakpoint ($m) {
      width: 50%;
      flex-shrink: 0;
    }
  }

  // alignment right
  @include breakpoint($m) {
    &.has-media-on-the-right {
      .wp-block-media-text__media {
        order: 1;
      }
      .wp-block-media-text__content {
        & > * {
          margin-left: auto;
        }
      }
    }
  }

  &.alignwide {
    @include breakpoint ($sub-m) {
      margin-left: -$gap;
      margin-right: -$gap;
    }
    .wp-block-media-text__content {
      @include breakpoint($l) {
        padding: $gap-big;
      }
    }
    & + .wp-block-media-text.alignwide {
      @include negative-spacing-m(margin-top);
    }
  }

  // add side padding for full width
  &.alignfull {
    .wp-block-media-text__content {
      @include breakpoint($xl) {
        padding-left: calc((50vw - #{$max-width-l / 2}) / 2);
        padding-right: calc((50vw - #{$max-width-l / 2}) / 2);
      }
    }
    & + .wp-block-media-text.alignfull {
      @include negative-spacing-l(margin-top);
    }
  }

}

/* Only back-end
----------------------------------------------- */

.block-editor .wp-block-media-text {

}

